<template>
    <div class="lmui-toggle" :class="[customClass, size]">
        <div class="toggle-wrapper toggle-button clearfix"
             :class="[colorClass, {locked: locked}]"
        >
            <template v-if="!iconMode">
                <span class="toggle-off"
                      v-if="notActiveText"
                      @click="switchToggle"
                      :class="{'left-active': !active}"
                >
                    {{notActiveText}}
                </span>
            </template>

            <template v-if="iconMode">
                <span v-if="!active" @click="switchToggle" class="toggle-off toggle-off-icon" :class="{'left-active': !active}">
                    <svg :class="'icons'+ iconConf.size +' '+ (iconConf.color ? ('icon-' + iconConf.color) : 'icon-gray') +' '+ iconConf.id" >
                        <use :xlink:href="'#'+ iconConf.id"></use>
                    </svg>
                </span>
                <span v-else class="toggle-on toggle-on-icon" @click="switchToggle" :class="{'right-active': active}">
                    <svg :class="'icons'+ iconConf.size">
                        <use :xlink:href="'#'+ iconConf.id"></use>
                    </svg>
                </span>
            </template>

            <div class="toggle-button-wrap"
                 @click="switchToggle"
                 :class="{'toggle-button-active': active, 'toggle-button-not-active': !active}"
            >
                <div class="toggle-button-bg">
                    <div class="toggle-button-out"></div>
                    <div class="toggle-button-in"></div>
                    <div class="toggle-button-switch"></div>
                </div>
            </div>

            <template v-if="!iconMode">
                <span class="toggle-on"
                      v-if="activeText"
                      @click="switchToggle"
                      :class="{'right-active': active}"
                >
                    {{activeText}}
                </span>
            </template>
        </div>
    </div>
</template>

<script>
    import '../common/scss/common.scss';
    import './toggle.scss';

    let Toggle = {
        props: {
            customClass: {
                type: [String],
                default: '',
            },

            // if locked is true, can't change active status
            locked: {
                type: [Boolean],
                default: false,
            },
            /**
             * active status, true or false
             */
            isActiveAtInit: {
                type: [Boolean],
                default: false,
            },

            /**
             * active color class, the predefined values are "red", "green", "blue"
             * the class active-color-is-colorname will be added on .toggle-wrapper element
             */
            activeColor: {
                type: String,
                default: 'green',
            },

            /**
             * not-active color class, the predefined values are "red", "green", "blue"
             * the class not-active-color-is-colorname will be added on .toggle-wrapper element
             */
            notActiveColor: {
                type: String,
            },

            /**
             * the callback which will be executed as:
             * function callback(changedActiveStatus, $vm)
             */
            onSwitch: {
                type: Function,
            },

            /**
             * The text for not active(left) text
             */
            notActiveText: {
                type: String,
                default: 'OFF',
            },

            /**
             * The text for active(right) text
             */
            activeText: {
                type: String,
                default: 'ON',
            },

            /**
             * when this is passed in, the active/not-active text will be removed
             * the iconConf obj should be like:
             * {
             *   id: 'icon-settings',
             *   size: '18'
             * }
             */
            iconConf: {
                type: Object,
                default: function () {
                    return {};
                }
            },

            size: {
                type: String,
                default: 'normal' //'normal' or 'small' DEV-29307 UI: Toggle styling fixes
            }
        },
        computed: {
            colorClass: function () {
                let colorClassObj = {};
                colorClassObj['active-color-is-' + this.activeColor] = this.activeColor;
                colorClassObj['not-active-color-is-' + this.notActiveColor] = this.notActiveColor;
                return colorClassObj;
            },
            iconMode: function () {
                return !!this.iconConf.id;
            }
        },
        methods: {
            switchToggle: function (e) {
                if (this.locked) {
                    return;
                }

                //DEV-29307
                //If the toggle has labels on each side (eg. On/Off), clicking the label on either side should move the toggle to that side.
                //If the toggle has only one label (eg. Treat as glob), clicking the label should change the state of the toggle.
                let $el = $(e.currentTarget);
                let ifSingleText = (this.activeText && !this.notActiveText) || (!this.activeText && this.notActiveText);
                if(!ifSingleText && $el.hasClass('toggle-off') && $el.hasClass('left-active')){
                    return;
                }
                if(!ifSingleText && $el.hasClass('toggle-on') && $el.hasClass('right-active')){
                    return;
                }

                this.active = !this.active;
                this.$nextTick(function () {
                    if (this.onSwitch) {
                        this.onSwitch(this.active, this);
                    }
                });
            }
        },
        data: function () {
            return {
                active: this.isActiveAtInit
            };
        }
    };

    export default Toggle;

</script>



